
import Toast from 'vux/src/components/toast/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import {SubmitSuggestUrl} from '@/api/api'
export default{
data(){
return{
ToastShow:false,
ToastTest:'',
PopupShow:true,
UserInfo:{
name:'',
tel:'',
content:'',
origin:''
},
SelectList:[
{name:''},
{name:''},
{name:''},
{name:''},
{name:''},
{name:''},
{name:''},
{name:''},
{name:''},
{name:''},
{name:''},
{name:''},
{name:''}
]
}
},
watch:{
['UserInfo.origin'](){
console.log(this.UserInfo.origin)
}
},
methods:{
CheckInfo(){
if (this.UserInfo.name==="") {
this.ToastTest=''
this.ToastShow=true
return false
}
if (!this.checkMobile(this.UserInfo.tel)) {
this.ToastTest=''
this.ToastShow=true
return false
}
if (this.UserInfo.content==="") {
this.ToastTest=''
this.ToastShow=true
return false
}
if (this.UserInfo.origin==="") {
this.ToastTest=''
this.ToastShow=true
return false
}
return true
},
checkMobile(str){

var
re = /^1\d{10}$/
if (re.test(str)) {
return true
} else {
return false
}
},
SubmitAjax(){
this.$http.post(SubmitSuggestUrl,this.UserInfo).then((res)=>{
console.log(res.data)
})
},
Submit(){
if (this.CheckInfo()) {
this.SubmitAjax()
}

},
ClosePop(){
this.PopupShow=false
}
},
components:{
Toast,
Popup
}
}



